<template>
	<div class="exam-main" :style="{minHeight: wrapHeight + 'px'}">
		<div class="exam-content">
			<div v-if="nodata" class="no-data"></div>
			<template v-else>
				<div class="eaxm-content-main" v-show="!loading">
					<ul>
						<template v-for="(item, index) in questions">
							<li :key="index" :id="'q'+index">
								<div class="question-type">{{types[item.subjectType*1]}}</div>
								<div class="question-content">
									<div class="question-title">{{index+1}}. {{item.title}}</div>
									<van-radio-group class="question-options" v-model="item.answer" v-if="item.subjectType*1===0||item.subjectType*1===2">
										<template v-for="(option, i) in item.op">
											<van-radio :name="i+1" :disabled="true">
												{{option}}
												<template v-if="rightAnswer(item,i)">
													<van-icon name="success" />
												</template>
											</van-radio>
										</template>
									</van-radio-group>
									<van-checkbox-group class="question-options" v-model="item.answer" v-if="item.subjectType*1===1">
										<template v-for="(option, i) in item.op">
											<van-checkbox shape="square" :name="i+1" :disabled="true">
												{{option}}
												<template v-if="rightAnswer(item,i)">
													<van-icon name="success" />
												</template>
											</van-checkbox>
										</template>
									</van-checkbox-group>
								</div>
								<div style="color: red;">答案解析：{{item.answerAnalysis||'无'}}</div>
							</li>
						</template>
					</ul>
				</div>
			</template>
		</div>
	</div>
</template>

<script>
	import { mapState } from "vuex"
	const types = ['单选题', '多选题', '判断题']
	export default {
		name: 'exam',
		data() {
			return {
				contentH: 0,
				loading: true, // 加载中
				nodata: false, // 无数据
				types: types, // 题目类型
				total: 0, // 题目总数
				questions: [] // 题目
			}
		},
		computed: {
			...mapState(['info']),
			wrapHeight() {
				return this.contentH
			},
			rightAnswer() {
				return(item, index) => {
					let temp = item.trueAnswer.indexOf(index+1) > -1 || item.trueAnswer.indexOf((index+1).toString()) > -1
					return temp
				}
			}
		},
		mounted() {
			this.$nextTick(() => {
				this.contentH = document.documentElement.clientHeight
				this.getData()
			})
		},
		methods: {
			// 获取试卷内容
			async getData() {
				let params = {
					studentId: this.info.id,
					examinationId: this.$route.query.examid
				}
				let res = await this.$Request.get('/examination/getTestPaperByWeChat.do', params)
				this.loading = false
				if(res && res.code === 0) {
					this.formatQuestion(res)
				} else {
					this.nodata = true
				}
			},
			// 格式化问卷数据
			formatQuestion(res) {
				let obj = res.list || {}
				let temp = obj.answer ? JSON.parse(obj.answer) : []
				temp.forEach(item => {
					if(item.subjectType * 1 !== 1) {
						item.answer = item.answer && item.answer.length ? item.answer[0] : ''
					}
				})
				let temp1 = temp.filter(item => item.subjectType === 0) // 单选题
				let temp2 = temp.filter(item => item.subjectType === 1) // 多选题
				let temp3 = temp.filter(item => item.subjectType === 2) // 判断题
				let temp4 = [temp1, temp2, temp3]
				let arr = [...temp4[0], ...temp4[1], ...temp4[2]]
				this.questions = arr
				this.total = this.questions.length
				this.nodata = !this.questions.length
				console.log(this.questions)
			}
		}
	}
</script>

<style lang="scss" scoped>
	.exam-main {
		padding-bottom: 0;
	}
	
	.exam-content {
		padding-top: 0;
		.van-icon-success {
			margin-left: 2px;
			vertical-align: middle;
			color: #1aad19;
			font-size: 16px;
		}
	}
</style>


